@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.spinner::after{content:"";box-sizing:border-box;width:40px;height:40px;position:absolute;top:calc(50% - 20px);left:calc(50% - 20px);border-radius:50%}.spinner.smooth::after{border-top:4px solid #000;border-left:4px solid #fff;border-right:4px solid rgba(255,255,255,0);-webkit-animation:spinner 0.6s linear infinite;animation:spinner 0.6s linear infinite}@-webkit-keyframes spinner{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinner{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.c-footer{background-color:black;padding:0px 200px;height:175px;display:flex;align-items:center;justify-content:center}@media only screen and (max-width: 1200px){.c-footer{padding:0px 20px}}@media only screen and (max-width: 1200px){.c-footer__container{display:flex;align-items:center;justify-content:center;flex-direction:column}}.c-footer__item{display:flex;justify-content:center;align-items:center;margin-bottom:5px}.c-footer__item a{margin:10px}.c-footer__item a:hover img{transition-duration:0.3s;opacity:0.7}.c-footer__item img{width:30px}.c-footer__item p{margin:0}.c-footer__item h2{font-size:17px;color:white;margin-bottom:0px}@media only screen and (max-width: 768px){.c-footer__item h2{text-align:center}}

*{list-style-type:none}body{font-family:'Montserrat', sans-serif}p{color:white}.c-app{background-color:black}

